/*********** Nav Bar **********/
.navbar {
    position: fixed;
    background: #ffffffe3!important;
    /* background-image: linear-gradient(to right, #052c47, #012241d0);*/
}
#nav-dropdown-item{
  color: rgb(15, 15, 15);
  background-color: white;
  border-radius: 2px;
}
#nav-dropdown-item:hover{
font-weight: 500;
color: #eb5d1e;
}

.nav-item:hover .dropdown-menu {
  display: block !important;
}

.nav-sticky {
    position: fixed;
    background: #ffffffee!important;
    transition: .5s;
}

.navbar .navbar-brand {
    color: #000000;
    font-size: 30px;
    line-height: 0px;
    font-weight: 200;
}

.navbar .navbar-brand:hover {
    color: #000000b2;
    font-size: 30px;
    transition: .2s ease-in-out;
    font-weight: bold;
}

.navbar .navbar-brand img {
    max-height: 40px;
}

.navbar .navbar-nav .nav-link{
    color: #1a1a1a;
 }

 .navbar .navbar-nav .nav-link.active,
  .navbar .navbar-nav .nav-link:hover{
    font-weight: bold;
    color: #eb5d1e;
  }


@media (min-width: 992px) {
    .navbar {
        position: relative;
        width: 100%;
        background:#ffffffee!important;
    }
    
    .navbar.nav-sticky {
        padding: 10px 60px;
        background: #ffffffe3 !important;
    }
        
    .navbar a.nav-link {
        padding: 8px 15px;
        font-size: 14px;
        letter-spacing: 1px;
        text-transform: uppercase;
    }
}

@media (max-width: 991.98px) {   
    .navbar {
      position: relative;
      overflow: hidden;
      background: #ffffffee!important;
    }
    
    .navbar a.nav-link {
        padding: 5px;
    }
}


/* -------Welcome------ */
#welcome {
    width: 100%;
    height: 500px;
    background: #fef8f5;
    border-bottom: 2px solid #fcebe3;
  }
  
  #welcome h1 {
    margin: 0 0 10px 0;
    font-size: 48px;
    font-weight: 700;
    line-height: 56px;
    color: #4e4039;
  }
  
  #welcome h2 {
    color: #a08f86;
    margin-bottom: 50px;
    font-size: 24px;
  }
  
  #welcome .btn-findJob {
    font-family: "Raleway", sans-serif;
    font-weight: 500;
    font-size: 16px;
    text-decoration: none!important;
    letter-spacing: 1px;
    display: inline-block;
    border-radius: 3px;
    transition: 0.5s;
    color: #fff;
    background: #eb5d1e;
  }
  
  #welcome .btn-findJob:hover {
    background: #ef7f4d;
  }
  
  #welcome .animated {
    animation: up-down 2s ease-in-out infinite alternate-reverse both;
  }
  
  @media (min-width: 1024px) {
    #welcome {
      background-attachment: fixed;
    }
  }
  
  @media (max-width: 991px) {
    #welcome {
      height: calc(100vh - 72px);
    }
    #welcome .animated {
      -webkit-animation: none;
      animation: none;
    }
    #welcome .welcome-img {
      text-align: center;
    }
    #welcome .welcome-img img {
      width: 50%;
    }
  }
  
  @media (max-width: 768px) {
    #welcome h1 {
      font-size: 28px;
      line-height: 36px;
    }
    #welcome h2 {
      font-size: 18px;
      line-height: 24px;
      margin-bottom: 30px;
    }
    #welcome .welcome-img img {
      width: 70%;
    }
  }
  
  @media (max-width: 575px) {
    #welcome .welcome-img img {
      width: 80%;
    }
  }
  
  @-webkit-keyframes up-down {
    0% {
      -webkit-transform: translateY(10px);
              transform: translateY(10px);
    }
    100% {
      -webkit-transform: translateY(-10px);
              transform: translateY(-10px);
    }
  }
  
  @keyframes up-down {
    0% {
      -webkit-transform: translateY(10px);
              transform: translateY(10px);
    }
    100% {
      -webkit-transform: translateY(-10px);
              transform: translateY(-10px);
    }
  }

  /********** 404***********/
  .canvan404 {
    height: 100vh;
    width: 100vw;
    position: absolute;
    background-color: #fff;
    z-index: 1000000;
    opacity: 0;
    -webkit-animation: hide404 1s ease;
            animation: hide404 1s ease;
  }  
  @-webkit-keyframes hide404 {
    0% {
      opacity: 1;
    }
    90% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  }  
  @keyframes hide404 {
    0% {
      opacity: 1;
    }
    90% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  }
