.num1{
  -webkit-animation: 2s anime1 infinite;
  animation: 2s anime1 infinite;
}
.num2{
  -webkit-animation: 2s anime2 infinite;
  animation: 2s anime2 infinite;
}
.num3{
  -webkit-animation: 2s anime3 infinite;
  animation: 2s anime3 infinite;
}
.btn-style{
  border: 2px solid #eb5d1e;
  border-radius: 0;
  color: #fc4a04;
  padding: 10px 50px;
}
@-webkit-keyframes anime1 {
  20%{-webkit-transform: translateY(-40px);transform: translateY(-40px);}
  40%{-webkit-transform: translateY(40px);transform: translateY(40px);}
}
@keyframes anime1 {
  20%{-webkit-transform: translateY(-40px);transform: translateY(-40px);}
  40%{-webkit-transform: translateY(40px);transform: translateY(40px);}
}
@-webkit-keyframes anime2 {
  40%{-webkit-transform: translateY(-40px);transform: translateY(-40px);}
  60%{-webkit-transform: translateY(40px);transform: translateY(40px);}
}
@keyframes anime2 {
  40%{-webkit-transform: translateY(-40px);transform: translateY(-40px);}
  60%{-webkit-transform: translateY(40px);transform: translateY(40px);}
}
@-webkit-keyframes anime3 {
  60%{-webkit-transform: translateY(-40px);transform: translateY(-40px);}
  80%{-webkit-transform: translateY(40px);transform: translateY(40px);}
}
@keyframes anime3 {
  60%{-webkit-transform: translateY(-40px);transform: translateY(-40px);}
  80%{-webkit-transform: translateY(40px);transform: translateY(40px);}
}
  /********** Footer***********/

  #footer {
    position: bottom;
    bottom: 0;
  }
  #footer .footer-top {
    padding: 30px;
    background: #fff;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.1) !important;
  }
  
  #footer .footer-top h4 {
    font-size: 16px;
    font-weight: bold;
    color: #212529;
    position: relative;
    padding-bottom: 12px;
  }
  
  #footer .footer-top .footer-links {
    margin-bottom: 20px;
  }
  
  #footer .footer-top .footer-links ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  #footer .footer-top .footer-links ul i {
    padding-right: 2px;
    color: #fc4a04;
    font-size: 12px;
    line-height: 1;
  }
  
  #footer .footer-top .footer-links ul li {
    padding: 10px 0;
    display: flex;
    align-items: center;
  }
  
  #footer .footer-top .footer-links ul li:first-child {
    padding-top: 0;
  }
  
  #footer .footer-top .footer-links ul a {
    font-size: 14px;
    color: #5c5c5c;
    transition: 0.3s;
    display: inline-block;
    line-height: 1;
  }
  
  #footer .footer-top .footer-links ul a:hover {
    text-decoration: none;
    color: #fc4a04;
  }
  
  #footer .footer-top .social-links a {
    font-size: 18px;
    display: inline-block;
    border-color: #fc4a04;
    color: rgb(44, 43, 43);
    line-height: 1;
    padding: 8px 0;
    margin-right: 4px;
    border-radius: 50%;
    text-align: center;
    width: 36px;
    height: 36px;
    transition: 0.3s;
  }
  
  #footer .footer-top .social-links a:hover {
    background: #eb5d1e;
    color: #fff;
    text-decoration: none;
  }
  
  #footer .copyright {
    text-align: center;
    color: #eb5d1e;
    float: left;
  }
  
  #footer .credits {
    float: right;
    text-align: center;
    font-size: 13px;
    color: #212529;
  }
  
  #footer .credits a {
    color: #fc4a04;
  }

  #footer .brderline {
   border-bottom: 2px solid #eb5d1e;
  }
  
  @media (max-width: 575px) {
    #footer .copyright, #footer .credits {
      float: none;
      text-align-last: center;
    }
  }
  

